
import XHeader from 'vux/src/components/x-header/index.vue'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'

export default {
components: {
XHeader,InlineLoading
},
data(){
return{
info: {},
}
},
mounted() {
this.getHelpDetail();
},
methods: {
getHelpDetail() {
this.$vux.loading.show({
text: '加载中...'
});
this.$ajax.post(this.$root.host,{
"MSH.1": "commonProblem",
"MSH.2": "getAvailableProblem",
"cbm": this.$route.params.CBM,
'token':this.$root.loginInfo.token,
}).then(res => {
console.log(res);
this.$vux.loading.hide();
if (typeof res != 'undefined') {
this.info = res;
}
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
})
}
}
}



